import React, {useEffect} from 'react';
import {useRef} from 'react';
import {useHistory} from 'react-router-dom';

function Adoptpage(props) {

	    const history = useHistory();
        const fnameRef= useRef();
        const lnameRef= useRef();
        const bdRef= useRef();
        const addressRef= useRef();
        const aptRef= useRef();
        const cityRef= useRef();
        const stateRef= useRef();
        const zipRef= useRef();
        const emailRef= useRef();
        const phoneRef= useRef();
        

	

	function submitHandeler(event){
		event.preventDefault();
		const enteredFname = fnameRef.current.value;
		const enteredLname = lnameRef.current.value;
		const enteredBd = bdRef.current.value;
		const enteredAddress = addressRef.current.value;
		const enteredApt = aptRef.current.value;
		const enteredCity = cityRef.current.value;
		const enteredState = stateRef.current.value;
		const enteredZip = zipRef.current.value;
		const enteredEmail = emailRef.current.value;
		const enteredPhone = phoneRef.current.value;

		const newData= {
			fname: enteredFname,
			lname: enteredLname,
			bd: enteredBd,
			address: enteredAddress,
			apt: enteredApt,
			city: enteredCity,
			state: enteredState,
			zip: enteredZip,
			email: enteredEmail,
			phone: enteredPhone

		};
		fetch(
			'https://pet-adoption-80366-default-rtdb.firebaseio.com/adopt.json',
			{
				method: 'POST',
				body: JSON.stringify(newData),
				header:{
					'Content-Type': 'application/json'
				},

			}
		).then(() => {
			history.replace('/petadoption');
		})

	
}






	useEffect(() => {
    window.scrollTo(0, 0)
   }, [])
	return(
	<div>
		
		  <div className='adoptContainer'>
			<img className='adoptImg' src={props.image} alt='' />
		   <div className='adoptBox'>
			<h1 className='classes.h1'>{props.text} </h1>
			<h4 className='classes.h4'>{props.age} old</h4>
			<h4 className='classes.h4'>{props.gender} </h4>
		   </div>
		  </div>
			<div>
		 <form onSubmit={submitHandeler}>
			 <h2 className='h2Adopt'>Adoption Application Form</h2>
			 <div className='formContainer'>
			 	<label htmlFor='fname'>Applicant First Name:</label>
                <input type='text' required id='fname' ref={fnameRef}></input>
                <label htmlFor='lname'>Applicant Last Name:</label>
                <input type='text' required id='lname' ref={lnameRef}></input>
                <label htmlFor='bday'>Date of Birth:</label>
                <input type='date' required id='bd' ref={bdRef}></input>
                <label htmlFor='address'>Address:</label>
                <input type='text' required id='address' ref={addressRef}></input>
                <label htmlFor='address' >Apt:</label>
                <input type='text'required id='apt' ref={aptRef}></input>
                <label htmlFor='address'>City:</label>
                <input type='text' required id='city' ref={cityRef}></input>
                <label htmlFor='address'>State:</label>
                <input type='text' required id='state' ref={stateRef}></input>
                <label htmlFor='address'>ZIP:</label>
                <input type='text' required id='zip' ref={zipRef}></input>
                <label htmlFor='email'>Email:</label>
                <input type='email' required id='email' ref={emailRef}></input>
                <label htmlFor='phone'>Phone:</label>
                <input type='number' required id='phone'ref={phoneRef}></input>

                <label htmlFor='fname'>Co-Applicant First Name:</label>
                <input type='text'></input>
                <label htmlFor='lname'>Co-Applicant Last Name:</label>
                <input type='text'></input>
                <label htmlFor='bday'>Date of Birth:</label>
                <input type='date'></input>
                <label htmlFor='address'>Address:</label>
                <input type='text'></input>
                <label htmlFor='address'>Apt:</label>
                <input type='text'></input>
                <label htmlFor='address'>City:</label>
                <input type='text'></input>
                <label htmlFor='address'>State:</label>
                <input type='text'></input>
                <label htmlFor='address'>ZIP:</label>
                <input type='text'></input>
                <label htmlFor='email'>Email:</label>
                <input type='email'></input>
                <label htmlFor='phone'>Phone:</label>
                <input type='number'></input>
                
                <div className='questions'> 
	                <label  htmlFor=''>Has anyone in the household ever been convicted of domestic battery, animal cruelty or violent crime?:</label>
	                <div><label className='checkbox'><input type='checkbox' name='choice-radio'/>yes</label><label className='checkbox'><input type='checkbox' name='choice-radio'/>no</label></div>
                
	                <label htmlFor=''>Do you: (Check One)</label>
	                <div><label className='checkbox'><input type='checkbox' name='choice-radio'/>Own</label><label className='checkbox'><input type='checkbox' name='choice-radio'/>Rent</label><label className='checkbox'><input type='checkbox' name='choice-radio'/>Live with relatives</label></div>
              
                
	                <label htmlFor=''>How often do you travel?:</label>
	                <input type='text'></input>
	                <label htmlFor=''>How will you provide for your pet when you travel?</label>
	                <input type='text'></input>
	                <label htmlFor=''>Does applicant work: </label>
	                <div><label className='checkbox'><input type='checkbox' name='choice-radio'/>full-time</label><label className='checkbox'><input type='checkbox' name='choice-radio'/>part-time</label><label className='checkbox'><input type='checkbox' name='choice-radio'/>Attend school</label><label className='checkbox'><input type='checkbox' name='choice-radio'/>Retired</label></div>
	                <label htmlFor=''>Does co-applicant work: </label>
	                <div ><label className='checkbox'><input type='checkbox' name='choice-radio'/>full-time</label><label className='checkbox'><input type='checkbox' name='choice-radio'/>part-time</label><label className='checkbox'><input type='checkbox' name='choice-radio'/>Attend school</label><label><input type='checkbox' name='choice-radio'/>Retired</label></div>
	                
	                <label htmlFor=''>Will your dog generally be kept: </label>
	                <div><label className='checkbox'><input type='checkbox' name='choice-radio'/>inside</label><label className='checkbox'><input type='checkbox' name='choice-radio'/>outside</label><label className='checkbox'><input type='checkbox' name='choice-radio'/>inside/outside</label></div>
                </div>
                 

            </div>
                   <div className='submit'><input className='btn' type="submit" value="Submit"/></div>

		 </form>
				
			
    
		</div>
	</div>
	);
}

export default Adoptpage;
