import {Link} from 'react-router-dom';
import {useState} from 'react';

function Nav() {

   const[navIsOpen, setNavIsOpen] = useState(false)

    function onClick(){
        setNavIsOpen(!navIsOpen)
    }

	return(
   <div>
	<div className='nav' onClick={onClick}>
		<div className='navbar'></div>
		<div className='navbar'></div>
		<div className='navbar'></div>
	</div>
	{navIsOpen ?
   <header>
   	<nav>
   		<ul>
   		    <li><Link className="navlink" to='/' onClick={onClick}>Home</Link></li>
   			<li><Link className="navlink" to='/adopt' onClick={onClick}>Adopt</Link></li>
   			<li><Link className="navlink" to='/about'onClick={onClick}>About Us</Link></li>
   			<li><Link className="navlink" to='/news' onClick={onClick}>News</Link></li>
   		</ul>
   	</nav>
   </header> : null}
    
   </div>
	);
}

export default Nav;


