import classes from './Main.module.css'
import passport from './passport.jpg';
import paw from './paw.png'
import {Link} from 'react-router-dom';
import {useState} from 'react';



import pup1 from './pup1.jpg';
import pup1a from './pup1.2.jpg';
import pup1b from './pup1.3.jpg';

function Petpage(props) {

     const [imgIsOpen, setImgIsOpen] = useState(0)

    function onClick(){
    	setImgIsOpen(true)
    }
   
    function offClick(){
    	setImgIsOpen(false)
    } 
	return(
	
	  <div>
	  
		<div className='petpage' >
			<img className='passportImg' src={passport} alt='' />
			<img  className='bigImg' src={props.image} alt='' />
			<img className='pawImg' src={paw} alt='' />
			<h2 className='classes.h2'>Hello my Name is :</h2>
			<h1 className='classes.h1'>{props.text} </h1>
			<h4 className='classes.h4'>I am: {props.age} old</h4>
			<h4 className='classes.h4'>My gender is : {props.gender} </h4>
			<p className='p'>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Amet est placerat in egestas erat. Ullamcorper a lacus vestibulum sed arcu non odio. Neque gravida in fermentum et sollicitudin ac orci phasellus. Ultricies tristique nulla aliquet enim. Suspendisse potenti nullam ac tortor vitae purus faucibus ornare. Aliquam ultrices sagittis orci a scelerisque purus semper eget duis. Vel elit scelerisque mauris pellentesque. Blandit turpis cursus in hac. Ac odio tempor orci dapibus ultrices.</p>
			 
			 <div >
				<div className='polaroid2' >
				 <img  className='centerImg' src={pup1a} alt='' onClick={onClick}  />
				</div> 	
				<div className='polaroid2' >
				 <img  className='centerImg' src={props.image} alt='' onClick={onClick}/>
				</div>
				<div className='polaroid2' >
				 <img  className='centerImg' src={pup1b} alt=''  onClick={onClick}/>
				</div>
			 </div>
            {imgIsOpen ?
			    <div className='polaroid3' onClick={offClick}>
				 <img  className='centerXLImg' src={pup1a} alt='' />
				 <img  className='centerXLImg' src={pup1} alt='' />
				 <img  className='centerXLImg' src={pup1b} alt='' />
				</div>


			: null }
			<p className='p'>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Amet est placerat in egestas erat. Ullamcorper a lacus vestibulum sed arcu non odio. Neque gravida in fermentum et sollicitudin ac orci phasellus. Ultricies tristique nulla aliquet enim. Suspendisse potenti nullam ac tortor vitae purus faucibus ornare. Aliquam ultrices sagittis orci a scelerisque purus semper eget duis. Vel elit scelerisque mauris pellentesque. Blandit turpis cursus in hac. Ac odio tempor orci dapibus ultrices.</p>
            <Link to={props.link}><button >Adopt Me</button></Link>
		</div>  
		
          
    

			<a href="https://www.vecteezy.com/free-vector/holiday">Holiday Vectors by Vecteezy</a> 
	</div> 
	);
}

export default Petpage;

